<template>
  <li>
    <div class="grid-container">
      <div
        class="grid-item grid-item-poster"
        style="grid-column: 1 / 2; grid-row: 1 / span 3"
      >
        <img :src="posterLink" style="width: 140px" />
      </div>
      <div class="grid-item grid-item-details" style="grid-column: 2 / span 4">
        <h4>{{ rowId }}. {{ fileName }}</h4>
        <hr />
        <h5 class="flex-container">
          <span>{{ isHd ? `HD ${size}` : `${size}` }}</span>
          <span>{{
            imdbDetail
              ? `Imdb | &#129351; ${imdbDetail.imdbRating}/10 |  &#128587; ${imdbDetail.imdbVotes} |  &#128467; ${imdbDetail.Released}`
              : ""
          }}</span>
          <base-button
            v-if="!imdbDetail"
            mode="icon"
            @click="getImdb"
            style="float: right; font-size: 16px; padding: 8px 10px"
          >
            <div class="loader" v-if="isImdbLoader"></div>
            <span v-else>Imdb</span>
          </base-button>
        </h5>
        <hr />
      </div>
      <div
        class="grid-item grid-item-actions"
        style="grid-column: 2 / span 4; text-align: center"
      >
        <div
          class="flex-container"
          style="align-items: flex-end; justify-content: flex-end"
        >
          <div class="input-group" style="margin-right: 0.5rem">
            <input type="text" v-model.trim="secretInput" />
            <button type="button" @click="getDownloadLink">Download</button>
          </div>
          <base-button mode="icon" @click="downloadItem">
            <div class="loader" v-if="isGetCodeLoader"></div>
            <span v-else>&#128273;</span>
          </base-button>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: [
    "rowId",
    "fileName",
    "size",
    "posterLink",
    "imdbId",
    "isHd",
    "downloadLink",
    "imdbDetail",
  ],
  data() {
    return {
      secretInput: "",
      isImdbLoader: false,
      isGetCodeLoader: false,
    };
  },
  methods: {
    async getImdb() {
      try {
        this.isImdbLoader = true;
        let myHeaders = this.$store.getters.headerWithToken;

        let requestOptions = {
          method: "GET",
          headers: myHeaders,
        };

        const response = await fetch(
          `${this.$store.state.auth.thanosUrl}/api/ThanosSnap/Rating?imdbID=${this.imdbId}`,
          requestOptions
        );
        const responseData = await response.json();

        if (!response.ok) {
          const error = new Error(
            responseData.message || "Failed to fetch in IMDB Rate!"
          );
          throw error;
        }
        let imdb_Detail = responseData.Data[0];
        this.$store.commit("files/setImdbDetails", {
          rowId: this.rowId,
          imdbDetail: imdb_Detail,
        });
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
      this.isImdbLoader = false;
    },
    async downloadItem() {
      try {
        // this.$emit("setSecretLoader", { isLoading: true });
        this.error = "";
        this.isGetCodeLoader = true;
        await this.$store.dispatch("files/downloadFile", {
          rowId: this.rowId,
          downloadLink: this.downloadLink,
        });
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
      this.isGetCodeLoader = false;

      let msg = this.error
        ? this.error
        : `Secret code sent to "${this.$store.getters.username}" (Please check SPAM
        folder also). Row No ${this.rowId}`;
      this.$emit("setSecretLoader", {
        isLoading: this.error ? false : true,
        modelMsg: msg,
      });
    },
    async getDownloadLink() {
      try {
        let myHeaders = this.$store.getters.headerWithToken;
        myHeaders.append("Content-Type", "application/json");
        var raw = JSON.stringify({
          secretCode: this.secretInput,
        });
        let requestOptions = {
          method: "POST",
          headers: myHeaders,
          body: raw,
        };

        const response = await fetch(
          `${this.$store.state.auth.thanosUrl}/api/ThanosSnap/DownloadLink`,
          requestOptions
        );
        const responseData = await response.json();

        if (!response.ok) {
          const error = new Error(
            responseData.message || "Failed to fetch in IMDB Rate!"
          );
          throw error;
        }
        console.log(responseData);
        this.downloadSource(responseData.DownloadURL);
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
    },
    downloadSource(source) {
      const fileName = this.fileName;
      var el = document.createElement("a");
      el.setAttribute("href", source);
      el.setAttribute("target", "_blank");
      el.setAttribute("download", fileName);
      document.body.appendChild(el);
      el.click();
      el.remove();
    },
  },
};
</script>

